.Header{
  font-size: 15px;
  width: 45px;
  box-shadow: 0 0 0 1px rgba(#fff, .1);
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  backdrop-filter: blur(10px);
  .name{
    color: #eee;
    text-align: right;
    position: absolute;
    line-height: 1;
    right: 50%;
    top: -.5em;
    transform-origin: 100% 50%;
    transform: rotate(-90deg);
    div{
      white-space: nowrap;
      letter-spacing: .1em;
      &.last{
        letter-spacing: .3em;
        margin-top: .3em;
      }
    }
  }
  .menu{
    gap: 1em;
    display: flex;
    flex-direction: column;
    margin-top: 10em;
    .up, .down{
      opacity: 0;
      pointer-events: none;
      transition: .2s ease;
      &.active{
        opacity: 1;
        pointer-events: initial;
        @media screen and (min-width: 380px){
          opacity: 0;
        }
      }
    }
    &:hover{
      .active{
        opacity: 1;
      }
    }
    .menu-item{
      display: flex;
      flex-direction: column;
      cursor: pointer;
      &::before{
        content: '';
        width: 30px;
        height: 30px;
        border-radius: 7px;
        background-size: 75% auto;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url(../../../assets/images/pro.svg);
        opacity: .5;
        transition: .2s .1s ease;
        cursor: pointer;
      }
      &.up:before{
        transform: rotate(180deg);
      }
      &.down:before, &.up:before{
        height: 20px;
        background-image: url(../../../assets/icons/down.svg);
        background-size: 70% auto;
      }
      .year{
        color: #fff;
        font-size: .6em;
        letter-spacing: .1em;
        text-align: center;
        opacity: .7;
        transition: .2s .1s ease;
      }
      &:hover{
        &:before{
          transition: .2s ease;
          opacity: .8;
          filter: drop-shadow(0 0 2px dodgerblue) drop-shadow(0 0 0px dodgerblue) drop-shadow(0 0 2px dodgerblue);
        }
        .year{
          color: #fff;
          font-size: .6em;
          letter-spacing: .1em;
          text-align: center;
          transition: .2s ease;
          opacity: 1;
          filter: drop-shadow(0 0 1px dodgerblue) drop-shadow(0 0 0px dodgerblue) drop-shadow(0 0 2px dodgerblue);
        }
      }
      
    }
  }
}